/*********RWD 平板992px********/ 
@mixin pad{
  @media (max-width:992px) {
    @content;
  }
}

/*********RWD 手機767px********/ 
@mixin mobile{
  @media (max-width:767px) {
    @content;
  }
}

/*********RWD 手機576px********/ 
@mixin phone{
  @media (max-width:576px) {
    @content;
  }
}